import React from 'react'

const Notification = ({ message, type }) => {
  const errorColor = type === 'error' ? 'red' : 'green'

  const notificationStyle = {
    color: errorColor,
    borderStyle: 'solid',
    borderRadius: 5,
    font: 25,
    padding: 5,
    margin: 5
  }

  if (message === null) {
    return null
  } else {
    return (
      <div style={notificationStyle}>
        {message}
      </div>
    )
  }
}

export default Notification